.am-icon {
  fill: currentColor;
  background-size: cover;
  width: 5.867vw;
  height: 5.867vw;
}
.am-icon-xxs {
  width: 4vw;
  height: 4vw;
}
.am-icon-xs {
  width: 4.8vw;
  height: 4.8vw;
}
.am-icon-sm {
  width: 5.6vw;
  height: 5.6vw;
}
.am-icon-md {
  width: 5.867vw;
  height: 5.867vw;
}
.am-icon-lg {
  width: 9.6vw;
  height: 9.6vw;
}
.am-icon-loading {
  -webkit-animation: cirle-anim 1s linear infinite;
          animation: cirle-anim 1s linear infinite;
}
@-webkit-keyframes cirle-anim {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes cirle-anim {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
